
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
components: {
XHeader
},
data() {
return {
info: {},
knjbList: [],
tjksList: [],
}
},
beforeRouteLeave(to, from, next) { // 用于做缓存

if (to.name == 'symptoms' || to.name == 'hospitalList') {
localStorage.setItem('symptom_cbm', this.$route.params.CBM || window.localStorage.symptom_cbm)
} else {
localStorage.removeItem('symptom_cbm');
}
next();
},
mounted() {

this.$refs.scroll.$el.style.height = document.body.clientHeight - 56 + "px";
this.getDetailInfo();
},
methods: {

getDetailInfo() {
this.$vux.loading.show({
text: '加载中...'
});
this.$ajax.post(this.$root.host, {
"MSH.1": "autodiagnosis",
"MSH.2": "getSymptom",
"cbm": this.$route.params.CBM||window.localStorage.symptom_cbm,
token: this.$root.loginInfo.token,
}).then(res => {
this.$vux.loading.hide();
if (typeof res != 'undefined') {
if (typeof res.jbList != 'undefined') {
this.knjbList = res.jbList;
}
if (typeof res.symptom != 'undefined') {
this.info = res.symptom;
this.tjksList = res.symptom.CTJJZKS;
}
}
}).catch(error => {
console.log(error);
this.$vux.loading.hide();
})
},
tohospital(item) {//医院列表
this.$router.push({
name: "hospitalList",
params: {
type: item.CMC,//科室名称
hospitalbm:item.CBM
}
});
},
tosymptoms(name, code) { //疾病详情
this.$router.push({
name: "symptoms",
params: {
name: name,
code: code,
}
});
},
}
}



